import bottom_tab_box from '../components/bottom_tab_box.vue'

export default {
  name: 'index',
  data (){
    return {
      medicalArchival:'',
      user:'',
    }
  },
  created(){
    var store_arr=[
      'medicalArchival',
      'user'
    ];
    for(var i in store_arr){
      this[store_arr[i]]=this.$store.state[store_arr[i]];
    };
  },
  components:{bottom_tab_box},
  methods:{
  }
}




